<template>
  <div class="container">
    <!--------------------------------------------------------------------------------------- 面包屑导航 -->
    <el-breadcrumb separator-class="el-icon-arrow-right" v-if="this.$route.query.id">
      <el-breadcrumb-item>学科管理</el-breadcrumb-item>
      <el-breadcrumb-item>{{this.$route.query.name}}</el-breadcrumb-item>
      <el-breadcrumb-item>目录管理</el-breadcrumb-item>
    </el-breadcrumb>
    <div class="header">
      <!-------------------------------------------------------------------------------------- form表单 -->
      <el-form :inline="true" class="demo-form-inline" :model="form" ref="fw">
        <el-form-item label="目录名称" class="input">
          <el-input v-model="form.directoryName"></el-input>
        </el-form-item>
        <el-form-item label="状态">
          <el-select placeholder="请选择" v-model="form.state">
            <el-option label="启用" value=0></el-option>
            <el-option label="禁用" value=1></el-option>
          </el-select>
        </el-form-item>
        <el-form-item>
          <el-button @click="fn">清除</el-button>
          <el-button type="primary" @click="filte" v-if="!this.$route.query.id">搜索</el-button>
          <el-button type="primary" @click="fin" v-else>搜索</el-button>
        </el-form-item>
      </el-form>
      <!------------------------------------------------------------------------------------ 文字链接 按钮 添加弹窗 -->
           <div class="link">
        <el-link type="primary" @click="router" v-if="this.$route.query.id"><i class="el-icon-back"></i>返回学科</el-link>
        <el-button icon="el-icon-edit" type="success" @click="open('add')"
          >新增目录</el-button
        >
        <el-dialog
          :title="title"
          :visible.sync="dialogFormVisible"
          width="400px"
          height="282px"
          @close="out"
        >
          <el-form :model="info" :rules="rules" ref="form">
            <el-form-item label="所属学科" prop="subjectID" v-if="!(this.$route.query.id)">
              <el-select
                placeholder="请选择"
                style="width: 280px"
                v-model="info.subjectID"
              >
                <el-option
                  v-for="item in simple"
                  :key="item.value"
                  :value="item.value"
                  :label="item.label"
                ></el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="目录名称" prop="directoryName" >
              <el-input
                style="width: 280px"
                v-model="info.directoryName"
              ></el-input>
            </el-form-item>
          </el-form>
          <div slot="footer" class="dialog-footer">
            <el-button @click=" out">取 消</el-button>
            <el-button type="primary" @click="dialog">确 定</el-button>
          </div>
        </el-dialog>
      </div>
    </div>
    <!---------------------------------------------------------------------------------------- 表单数据计数 -->
    <div class="alert">
      <el-alert type="info" show-icon :closable="false" :title="`一共有${isState}条`"> </el-alert>
    </div>
    <!-----------------------------------------------------------------------------------------表格-->
    <el-table
      :data="lis"
      style="width: auto"
      :header-cell-style="{
        background: '#EFEFEF',
      }"
    >
      <el-table-column type="index" label="序号" width="75px">
      </el-table-column>
      <el-table-column prop="subjectName" label="所属学科"> </el-table-column>
      <el-table-column prop="directoryName" label="目录名称"> </el-table-column>
      <el-table-column prop="username" label="创建者"> </el-table-column>
      <el-table-column prop="addDate" label="创建日期">
        <template slot-scope="scope"
          ><span> {{ scope.row.addDate | parseTimeByString }}</span></template
        >
      </el-table-column>
      <el-table-column prop="totals" label="面试题数量"> </el-table-column>
      <el-table-column prop="state" label="状态">
        <template slot-scope="scope"
          ><span>
            {{ scope.row.state == false ? "已禁用" : "已启用" }}</span
          ></template
        ></el-table-column
      >
      <el-table-column label="操作" width="150px">
        <template slot-scope="scope">
          <div class="cells">
            <el-button type="text" @click="handle(scope.$index, scope.row)">{{
              scope.row.state === false ? "启用" : "禁用"
            }}</el-button>
            <el-button
              type="text"
              @click="open('look', scope.$index, scope.row)"
              :disabled="
                scope.row.state === true
                  ? (scope.row.state = true)
                  : (scope.row.state = false)
              "
              >修改</el-button
            >
            <el-button
              type="text"
              @click="handleDelete(scope.$index, scope.row)"
              :disabled="
                scope.row.state === true
                  ? scope.row.state === true
                    ? (scope.row.state = true)
                    : (scope.row.state = false)
                  : scope.row.totals !== 0
                  ? true
                  : false
              "
              >删除</el-button
            >
          </div>
        </template>
      </el-table-column>
    </el-table>
    <!-- ------------------------分页器---------------------------------------------- -->
    <div class="pagination"  v-if = "isState >=10 ">
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="currentPage"
        :page-sizes="[5, 10, 15, 20]"
        :page-size="pageSet.pageSize"
        layout=" sizes, prev, pager, next, jumper"
        :total="isState"
        background
      >
      </el-pagination>
    </div>
  </div>
</template>

<script>
import { simple } from '../../api/hmmm/subjects'
import { createAPI } from '@/utils/request'
import { mapActions, mapState } from 'vuex'
export default {
  data () {
    return {
      ids: '',
      simple: [],
      title: '',
      form: {
        directoryName: '',
        state: null
      },
      total: '',
      dtype: '',
      info: {
        id: '',
        subjectID: '',
        directoryName: '',
        subjectName: ''
      },
      dialogFormVisible: false,
      currentPage: 1,
      pageSet: {
        page: 1,
        pagesize: 10
      },
      rules: {
        subjectID: [
          { required: true, message: '请选择所属学科', trigger: 'change' }
        ],
        directoryName: [
          { required: true, message: '请输入目录名称', trigger: 'blur' }
        ]
      }
    }
  },
  // 重新点击目录重新刷新
  watch: {
    $route: function (to, from) {
      // 执行需要更新的方法
      this.getList(this.pageObj)
    }
  },
  computed: {
    ...mapState('questionsChoice/directory', ['lis', 'isState'])
  },
  methods: {
    ...mapActions('questionsChoice/directory', ['getList']),
    ...mapActions('questionsChoice/directory', ['getnewlist']),
    ...mapActions('questionsChoice/directory', ['getState']),
    router () {
      this.$router.push('/subjects/list')
    },
    fin () {
      this.form.state ? this.getList({ subjectID: this.$route.query.id, state: this.form.state }) : this.getList({ subjectID: this.$route.query.id })
    },
    async con () {
      const res = await simple()
      // console.log(res, '简单数据')
      this.simple = res.data
    },
    // 取消按钮
    out () {
      this.$refs.form.resetFields()
      this.dialogFormVisible = false
      this.info.subjectID = ''
      this.info.directoryName = ''
    },
    // 搜索按钮  //
    async  filte () {
      if (this.form.directoryName === '' && this.form.state === '') {
        this.getList(this.pageSet)
      } else {
        this.getList(this.form)
      }
    },
    // 清除按钮
    fn () {
      this.form.directoryName = ''
      this.form.state = ''
    },
    // 确定按钮
    async dialog () {
      await this.$refs.form.validate()
      if (this.dtype === 'look') {
        await createAPI(`/directorys/${this.info.id}`, 'put', this.info)
        this.$message.success('修改目录成功')
        this.dialogFormVisible = false
      } else {
        this.$route.query.id ? await createAPI('/directorys', 'post', { subjectID: +(this.$route.query.id), directoryName: this.info.directoryName })
          : await createAPI('/directorys', 'post', { subjectID: this.info.subjectID, directoryName: this.info.directoryName })
        this.$message.success('新增目录成功')
        this.dialogFormVisible = false
      }
      // 渲染
      this.getList({ subjectID: this.$route.query.id })
    },
    // 改变页数
    async handleSizeChange (val) {
      this.pageSet.page = 1
      this.pageSet.pageSize = val
      this.getList({ page: this.pageSet.page, pagesize: val })
    },
    async handleCurrentChange (val) {
      this.pageSet.page = val
      this.getList({ page: val, pagesize: this.pageSet.pageSize })
    },
    open (type, index, row) {
      if (this.simple === []) { this.out() }
      this.dialogFormVisible = true
      this.dtype = type
      if (this.dtype === 'add') {
        // console.log(1)
        this.title = '新增目录'
      } else {
        // console.log(2)
        this.title = '修改目录'
      }
      if (this.dtype !== 'add') {
        this.title = '修改目录'
        this.info.id = row.id
        this.info.subjectID = row.subjectID
        this.info.directoryName = row.directoryName
        this.info.subjectName = row.subjectName
        this.info.directoryName = row.directoryName
      } else {
        this.title = '新增目录'
        this.info.subjectID = ''
        this.info.directoryName = ''
      }
    },
    // 删除功能
    async  handleDelete (index, row) {
      const res = await createAPI(`/directorys/${row.id}`, 'delete')
      console.log(res)
      this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        console.log('删除请求')
        this.getList()
        this.$message({
          type: 'success',
          message: '删除成功!'
        })
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '已取消删除'
        })
      })
    },
    // 点击启动按钮跳出弹窗
    async  handle (index, row) {
      row.state === true ? row.state = false : row.state = true
      row.state ? this.ids = 0 : this.ids = 1
      console.log({ state: this.ids, id: row.id })
      await createAPI(`/directorys/${row.id}/${this.ids}`, 'post')
      this.$message.success('操作成功')
    }
  },
  async created () {
    this.con()
    if (this.$route.query === {}) {
      this.getList(this.pageSet)
    } else {
      this.getList({ subjectID: this.$route.query.id })
    }
  }
}
</script>

